import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack, Typography} from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider,   RHFTextField, RHFSwitch } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
HabukhanBillCharge.propTypes = {
    discount: PropTypes.object,
  };
export default function HabukhanBillCharge({ discount}) {
    const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
bill: Yup.string().required(),
bill_max: Yup.string().required(),
bill_min: Yup.string().required(),
 });

  const defaultValues = useMemo(
    () => ({
    bill: discount?.bill,
    direct: discount?.direct,
    bill_max: discount?.bill_max,
    bill_min: discount?.bill_min
    }),
    [discount]
  );
  const AccessToken =  window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount!== undefined) {
      reset(defaultValues);
    }
   
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]); 
  const {
    reset,
    setError,
    handleSubmit,
    formState: {errors, isSubmitting },
  } = methods;
 
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/edit/bill/charges/account/${AccessToken}/habukhan/secure`,data)
     swal('success', `Bill Charges Edited`, 'success');
    } catch (error) {
        if (isMountedRef.current){
            setError('afterSubmit', error);
          }
    }
  };
   
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
     
        <Grid item xs={12} md={8}>
        {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
     
            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            > 
              <RHFTextField name="bill_min" label="Minimum Amount" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>  
              <RHFTextField name="bill_max" label="Maximum Amount" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>           
                 <RHFTextField name="bill" label="Charges" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
                 <RHFSwitch
              name="direct"
              labelPlacement="start"
              label={
                <>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                    Direct Charges
                  </Typography>
                </>
              }
             
              sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
            />
             </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
               Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
